<template>
    <div>
        <JCHeader :title="'索引列表'" :left-options="{showBack:true,backText:''}" :right-options="{showMore:true}"></JCHeader>
        <div class="page-indexlist-wrapper">
            <JCindexList>
                <JCindexSection v-for="(item,index) in alphabet" :index="item.initial" :key="index">
                    <JCCell v-for="(cell,index) in item.cells" :title="cell" :key="index">
                        <img slot="icon" src="@/assets/mobile/cu-pic2.jpg" width="24" height="24" style="margin-right:5px;" />
                    </JCCell>
                </JCindexSection>
            </JCindexList>
        </div>
    </div>
</template>
<script type="text/babel">
    const NAMES = ['Apple', 'Alden', 'Austin', 'Baldwin', 'Braden', 'Carl', 'Chandler', 'Clyde', 'David', 'Edgar', 'Elton', 'Floyd', 'Freeman', 'Gavin', 'Hector', 'Henry', 'Ian', 'Jason', 'Joshua', 'Kane', 'Lambert', 'Matthew', 'Morgan', 'Neville', 'Oliver', 'Oscar', 'Perry', 'Quinn', 'Ramsey', 'Scott', 'Seth', 'Spencer', 'Timothy', 'Todd', 'Trevor', 'Udolf', 'Victor', 'Vincent', 'Walton', 'Willis', 'Xavier', 'Yvonne', 'Zack', 'Zane'];
    export default {
        data() {
            return {
                alphabet: []
            };
        },
        created() {
            'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('').forEach(initial => {
                let cells = NAMES.filter(name => name[0] === initial);
                this.alphabet.push({
                    initial,
                    cells
                });
            });
        }
    };
</script>